<template>
  <!-- index.wxml -->
  <div class="checkPhone">
    <div class="form">
      <div class="title">请输入报名时的手机号</div>
      <VanField
        title-width="50"
        v-model="phone"
        bind:input="phoneChange"
        center
        clearable
        placeholder="请输入手机号"
        :border="false">
        <div slot="label" class="phone_lable">
          +86
          <text style="font-size:30px">▾</text>
        </div>
      </VanField>
      <VanButton
        type="primary"
        size="normal"
        block
        class="btn"
        @click="fenban">获取老师微信</VanButton>
      <div class="tips">
        <span>注意：<br />手机号是上课唯一凭证，只有正确输入报名手机号，才能添加班主任老师上课！</span>
      </div>
    </div>
    <VanDialog id="van-dialog" />
  </div>
</template>
<script>
import { mapMutations } from 'vuex'
import Store from '@/store'
import { getSearch, isPhone } from '@/utils'
import wxAuth from '@/helper/Auth/wxAuth'
export default {
  async beforeRouteEnter (to, from, next) {
    // 获取微信授权
    await wxAuth({ getTokenQuery: { ...getSearch() } })
    const { id } = Store.state.user.userInfo || {}
    Store.$log.update({ mid: id })
    next()
  },
  data () {
    return {
      phone: ''
    }
  },
  created () {
    this.getProductNameList()
  },
  methods: {
    ...mapMutations('user', ['SET_CLASS_INFO', 'SET_CLASS_NAME']),
    async fenban () {
      // 分班 校验手机号
      const phone = this.phone
      this.$log.update({
        alternate: JSON.stringify({
          buttonname: ''
        })
      })
      this.$log.click({
        module_part: '106_tf_h5_00057',
        module_ori: '表单提交模块'
      })
      if (!phone) {
        this.$log.update({
          alternate: JSON.stringify({
            buttonname: '请填写手机号'
          })
        })
        this.$log.click({
          module_part: '106_tf_h5_00058',
          module_ori: '表单错误模块'
        })
        return this.$toast('请填写手机号')
      }
      if (!isPhone(phone)) {
        this.$log.update({
          alternate: JSON.stringify({
            buttonname: '手机号有误,请重填'
          })
        })
        this.$log.click({
          module_part: '106_tf_h5_00058',
          module_ori: '表单错误模块'
        })
        return this.$toast('手机号有误,请重填')
      }
      // 分班
      this.$apis.user.fenban({
        phone,
        version: '1.1'
      }).then(data => {
        this.SET_CLASS_INFO(data)
        this.$router.push({ name: 'Question' })
      }).catch(err => {
        this.$log.update({
          alternate: JSON.stringify({
            buttonname: err.msg
          })
        })
        this.$log.click({
          module_part: '106_tf_h5_00058',
          module_ori: '表单错误模块'
        })
      })
    },
    async getProductNameList () {
      // 获取商品标题
      const data = await this.$apis.user.getProductNameList()
      this.SET_CLASS_NAME(data)
    }
  }
}
</script>
<style lang="scss" scoped>
.checkPhone {
  width: 750px;
  background: #ffffff;
  min-height: 100vh;
  padding-top: 100px;
  .form {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    height: 800px;
  }
  .title {
    width: 750px;
    height: 67px;
    font-size: 48px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #000000;
    line-height: 67px;
    text-align: center;
  }
  .tips {
    width: 614px;
    height: 126px;
    font-size: 24px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #bbbbbb;
    line-height: 42px;
    padding-top: 40px;
  }
  /deep/.van-cell {
    width: 614px;
    height: 98px;
    background: #f5f5f5;
    border-radius: 49px;
  }
  /deep/.van-field__label {
    width: 90px;
  }
  .phone_lable {
    font-size: 32px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #333333;
    width: 100px;
  }
  .btn {
    width: 614px;
    height: 98px;
    background: #1d53df;
    border-radius: 49px;
  }
}
</style>
